.road-card {
  padding: 10px;
  background-color: #eee;
  border-bottom: 1px solid #000;
}

.road-card h2 {
  font-size: 1.25rem;
  margin-bottom: 10px;
}

.road-card > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.road-card span {
  font-weight: bold;
}


