.landing {
  text-align: center;
  width: 100%;
  min-height: 100vh;
}

.landing h1 {
  margin: 0;
  padding: 0;
  color: #fff;
  margin-bottom: 40px;
  font-size: 3rem;
}

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 60%;
  margin: 60px auto;
}

.wrapper a {
  display: block;
  color: #fff;
  text-decoration: none;
  font-size: 1rem;
  padding: 20px;
  background-color: #7d7c7c;
  width: 27%;
  max-width: 300px;
  margin-bottom: 20px;
  -webkit-box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}

.wrapper a:hover {
  -webkit-box-shadow: none;
          box-shadow: none;
  background-color: #2d2d2d;
}




